<template>
  <!-- <div v-if="listItem && listItem.tourNo === Number(listId)"> -->
  <div class="px-[2%]">
    <div class="max-w-7xl mx-auto box-border mt-7">
      <router-link :to="'/'" class="cursor-pointer">
        <img src="@/assets/main_logo2.png" alt="logo">
      </router-link>
    </div>
    <div class="max-w-7xl mx-auto border box-border p-4 rounded-md mt-7">
      <h3 class="font-bold mb-2 text-xl">{{ listItem.tourNm }}</h3>
      <img :src="listItem.tourImg" alt="listItem.TITLE" :title="listItem.TITLE" class="w-full">
      <ul>
        <li class="mb-2 mt-4">시 군 : {{ listItem.areaSe }}</li>
        <li class="mb-2">주소 : {{ listItem.adres }}</li>
        <li class="mb-2">전화번호 : {{ listItem.telno }}</li>
        <li class="mb-2">운영시간 : {{ listItem.operTime }}</li>
        <li class="mb-2 leading-10">시설 설명 : {{ listItem.intrcn }}</li>
        <li><button @click="open_page(listItem.tourUrl)">여행정보</button></li>
      </ul>
      <div class="flex">
        <div id="map" class="w-2/4 h-96"></div>
        <div id="map2" class="w-2/4 h-96"></div>
      </div>
    </div>
  </div>
  <!--sessionStorage에 있는 listItem.tourNo와 detail 주소창에 있는 Id 번호가 같을 때만 detail 페이지를 띄운다. 만약 위의 v-if 비교구문이 없으면 detail 페이지에서 detail/Id 에서 Id에 있는 숫자가 아닌 아무 숫자나 문자를 적고 엔터를 쳐도 detail 페이지가 그대로 떠있다.-->
  <!-- isNAN - Not a Number - 파라미터 숫자일 경우 false / 숫자가 아닐 경우 true -->
  <!-- <button @click="$router.go(-1)">뒤로가기</button> -->
  <!-- </div> -->
    <!-- 3b8406835c2cf7876709f0a5404df440 -->
  <!-- <div v-else :click="$router.replace('/404')"> -->
  <!-- <button @click="$router.replace('/')">뒤로가기</button> -->
  <!-- @와 :(바인딩)의 차이점 : @는 내가 직접 선택했을때 이벤트가 발생하는 것이고, :(바인딩)은 내가 직접 선택하지 않아도 강제로 이벤트가 발생하는 것이다. 예를 들어 @click : 내가 직접 클릭을 했을때 이벤트가 발생함. :click : 내가 직접 클릭하지 않아도 이벤트가 발생함. -->
  <!-- </div> -->
  <div v-if="listItem && listItem.tourNo !== Number(listId)" :click="$router.replace('/404')"></div>
</template>

<script>
// 3b8406835c2cf7876709f0a5404df440
export default {
  data() {
    return {
      listId: this.$route.params.id,  // this.$route는 앞의 index.js 파일에서 path를 뜻한다.
      listItem: [],
      map: null,
      markers: [],
      latitude: 0,
      longitude:0,
    }
  },
  methods:{
    open_page(url){
      window.open(url);
    },
    initMap() {
      const container = document.getElementById("map");
      var roadviewContainer = document.getElementById("map2");
      var roadview = new kakao.maps.Roadview(roadviewContainer); //濡쒕뱶酉� 媛앹껜
      var roadviewClient = new kakao.maps.RoadviewClient(); //醫뚰몴濡쒕��� 濡쒕뱶酉� �뚮끂ID瑜� 媛��몄삱 濡쒕뱶酉� helper媛앹껜
      var position = new kakao.maps.LatLng(this.listItem.lat, this.listItem.lng); //吏��꾩쓽 以묒떖醫뚰몴.
      roadviewClient.getNearestPanoId(position, 50, function(panoId) {
    roadview.setPanoId(panoId, position); //panoId�� 以묒떖醫뚰몴瑜� �듯빐 濡쒕뱶酉� �ㅽ뻾
    }); 
      const options = {
        center: new kakao.maps.LatLng(this.listItem.lat, this.listItem.lng),
        level: 5,
      };
      this.map = new kakao.maps.Map(container, options);
      this.displayMarker([[this.listItem.lat, this.listItem.lng]]);
    },
    displayMarker(markerPositions) {
      if (this.markers.length > 0) {
        this.markers.forEach((marker) => marker.setMap(null));
      }

      const positions = markerPositions.map(
          (position) => new kakao.maps.LatLng(...position)
      );

      
      if (positions.length > 0) {
        this.markers = positions.map(
            (position) =>
                new kakao.maps.Marker({
                  map: this.map,
                  position,
                })
        );

        const bounds = positions.reduce(
            (bounds, latlng) => bounds.extend(latlng),
            new kakao.maps.LatLngBounds()
        );


        this.map.setBounds(bounds);
      }
    }



  },
  name: "DetailPage",
  created(){
    this.listItem = JSON.parse(sessionStorage.getItem('data'))
    console.log(this.listItem)
    console.log()
    if (!("geolocation" in navigator)) {
      return;
    }

    // get position
    // navigator.geolocation.getCurrentPosition(pos => {
    //   this.latitude = pos.coords.latitude;
    //   this.longitude = pos.coords.longitude;

    //   if (window.kakao && window.kakao.maps) {

    //     this.initMap();

    //   } else {
    //     const script = document.createElement("script");
    //     /* global kakao */
    //     script.onload = () => kakao.maps.load(this.initMap);
    //     script.src = "//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=3b8406835c2cf7876709f0a5404df440";
    //     document.head.appendChild(script);
    //   }

    // }, err => {
    //   alert(err.message);
    // })
    if (window.kakao && window.kakao.maps) {

this.initMap();

} else {
const script = document.createElement("script");
/* global kakao */
script.onload = () => kakao.maps.load(this.initMap);
script.src = "//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=3b8406835c2cf7876709f0a5404df440";
document.head.appendChild(script);
}
  },
  computed:{
    
  },
  mounted(){
  },
}
</script>

